.followingBtn {
  font-family: 'Montserrat';
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #373737;
  border: none;
  text-transform: uppercase;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  width: 196px;
  height: 50px;
  margin-left: 92px;
  background: #5cd3a8;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
}
.followingBtn:hover,
.followingBtn:focus {
  background-color: #ee82ee;
}
